import React, { useState } from "react"
import { PrivateSharedContentOwner } from "@rp/react/tanstack-api/file"
import { OptionsSelect } from "@rp/react/web/common/composite-components"
import { Box } from "@rp/react/web/mui/core"
import { SelectItem } from "../../../filesPage.types"
import { convertAPIUserToSelectItem } from "../../../utils/convertAPIUserToSelectItem"
import { CustomOwnersSelectorLine } from "../CustomOwnersSelectorLine/CustomOwnersSelectorLine"

type FilesPaneFiltersProps = {
  setOwnersSelected: (owners: Array<SelectItem>) => void
  ownersList: Array<PrivateSharedContentOwner> | undefined
  isOwnersListPending: boolean
  ownersSelected: Array<SelectItem>
}

function FilesPaneFilters({
  setOwnersSelected,
  ownersList,
  isOwnersListPending,
  ownersSelected,
}: FilesPaneFiltersProps) {
  const [ownerSearch, setOwnerSearch] = useState("")
  const items: Array<SelectItem> = ownersList?.map(convertAPIUserToSelectItem) || []

  return (
    <Box sx={{ display: "flex", mt: 1.5 }}>
      <OptionsSelect
        isLoading={isOwnersListPending}
        label="Owner"
        items={items}
        placeholder="Find an Owner"
        selected={ownersSelected}
        setSelected={setOwnersSelected}
        search={ownerSearch}
        setSearch={setOwnerSearch}
        CustomSelectionLine={CustomOwnersSelectorLine}
      />
    </Box>
  )
}

export { FilesPaneFilters }
